$base-color: rgb(var(--base-color));

$text-color: rgb(var(--text-color-1));

$bg-1: rgb(var(--bg-1));
$bg-2: rgb(var(--bg-2));

$blue-color: rgb(var(--blue-color));
$error-box-color: rgb(237, 112, 90);

$blackout-color: rgba(var(--blackout-color), .2);

$icon-color: rgb(var(--icon-color));
$icon-button-bg-1: rgb(var(--icon-button-bg-1));
$icon-button-bg-2: rgb(var(--icon-button-bg-2));

$icon-theme-bg: rgb(var(--icon-theme-bg));
$icon-theme-color: rgb(var(--icon-theme-color));

$shadow-1-color: var(--shadow-1-color);
$shadow-2-color: var(--shadow-2-color);

$border-color-1: var(--border-1);
$border-color-2: var(--border-2);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  font-family: 'Roboto Condensed', sans-serif;
  height: 100%;
}

body {
  height: 100%;
  background-color: rgb(var(--bg-2));
  font-size: 1.6rem;
}

a { 
  color: inherit; 
} 


:root {
 
  --text-color-1: 22, 30, 41;
  --bg-1: 255, 255, 255;
  --bg-2: 230, 230, 230;
  --blackout-color: 1, 1, 1;
  --icon-color: 230, 230, 230;
  --icon-button-bg-1: 255, 255, 255;
  --icon-button-bg-2: 190, 190, 190;
  --blue-color: 67, 87, 242;
  --icon-theme-bg: 33, 45, 62;
  --icon-theme-color: var(--icon-color);

  --border-1: 103, 112, 117;
  --border-2: 83, 92, 97;
  color: rgb(var(--text-color-1));
}

.dark_theme {
  --text-color-1: 255, 255, 255;
  --bg-1: 22, 30, 41;
  --bg-2: 33, 45, 62;
  --blackout-color: 255, 255, 255;
  --icon-color: var(--text-color-1);
  --icon-button-bg-1: var(--bg-1);
  --icon-button-bg-2: var(--bg-2);
  --blue-color: 67, 87, 242;
  --icon-theme-bg: 255, 255, 255;
  --icon-theme-color: var(--bg-2);

  color: rgb(var(--text-color-1));
}

@mixin work-space-container {
  width: clamp(40rem, 100%, 140rem);
}

.blackout {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  background-color: $blackout-color;
}

@mixin drop-shadow {
  box-shadow: -1rem -1rem 1.5rem rgba($shadow-1-color, .4),
   1.5rem 1.5rem 1.5rem 0 rgba($shadow-2-color, .2);
}

@mixin inner-shadow {
  box-shadow: inset -1rem -1rem 1rem 0 rgba($shadow-1-color, .4) ,
  inset 1.5rem 1.5rem 1rem rgba($shadow-2-color, .4);
}



@mixin button {
  border-radius: 50%;
  
}

@mixin button-text {
  border-radius: 2rem;
}

@mixin board {
  border-radius: 2rem;
  background-color: $bg-1;
}

@mixin icon-button($bg: $bg-2) {
  font-size: 2rem;
  display: flex;
  align-items: center;
  padding: 1rem;
  border: 1px solid $bg;
  border-radius: 2rem;
  cursor: pointer;
  transition: all .3s;

  &:hover {
    border: 1px solid $blue-color;
    color: $blue-color;
  }
}
.header {
  display: flex;
  justify-content: space-between;
  color: $text-color;

  .left {
    display: flex;
    align-items: center;
    
    .logo {
      font-size: 3rem;
      cursor: pointer;
      display: flex;
      align-items: center;

      i {
        font-size: 3rem;
      }

      &:hover {
        color: $blue-color;
      }
    }
  }

  .right {
    display: flex;
    //gap: 1rem;
    padding: 1rem;

    .theme {
      font-size: 2.6rem;
      cursor: pointer;
      align-items: center;
      margin: auto;
      background-color: $icon-theme-bg;
      color: $icon-theme-color;
      padding: .5rem;
      border-radius: 50%;

      i {
        width: 4rem;
        height: 4rem;
        line-height: 4rem;
        margin: auto;
        text-align: center;
        justify-content: center;
      }
      
    }

    .out {
      display: flex;
      margin-left: 1rem;
      cursor: pointer;
      font-size: 2rem;
      align-items: center;
      gap: 1rem;
      background-color: $icon-button-bg-1;
      border-radius: 3rem;
      padding: 1rem 2rem 1rem 1rem;
      border: 1px solid $icon-button-bg-1;

      
      &:hover {
        transition: all .2s;
        border: 1px solid $blue-color;
      }
      &:hover i {
        transition: all .2s;
        background-color: $blue-color;
      }
      i {
        font-size: 2.6rem;
        padding: .5rem;
        background-color: $icon-button-bg-2;
        color: $icon-color;
        border-radius: 50%;
      }

      span {
        align-items: center;
      }
    }
  }
}